raul-badge {
  display: inline-block;
  vertical-align: middle;
  font-size: 0.875rem;
}

raul-badge .r-badge__container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1.25rem;
  border-radius: 9999px;
  font-size: 0.75rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  min-width: 30px;
}

raul-badge .r-badge__container--primary {
  color: #fff;
  background-color: #0076CC;
}

raul-badge .r-badge__container--warning {
  color: #37474F;
  background-color: #FEC12D;
}

raul-badge .r-badge__container--error {
  color: #fff;
  background-color: #D01A1F;
}

raul-badge .r-badge__container--success {
  color: #fff;
  background-color: #139C3E;
}

raul-badge .r-badge__icon {
  position: relative;
  margin-right: 0.25rem;
}

raul-badge .r-badge__content {
  white-space: nowrap;
}